import React from "react";
import { motion } from "framer-motion";
import { Button } from "./ui/button";
import { profile } from "../data";
import {
  useReducedMotion,
  getFadeInUp,
  getStaggerContainer,
  getStaggerChild,
  getHoverScale,
} from "../lib/motion";

const Contact: React.FC = () => {
  const reducedMotion = useReducedMotion();
  const fadeInUp = getFadeInUp(reducedMotion);
  const staggerContainer = getStaggerContainer(reducedMotion, 0.1);
  const staggerChild = getStaggerChild(reducedMotion);
  const hoverScale = getHoverScale(reducedMotion);

  return (
    <section
      id="contact"
      className="py-24 px-4"
      aria-labelledby="contact-heading"
    >
      <div className="mx-auto max-w-4xl text-center">
        <motion.div
          variants={fadeInUp}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, margin: "-100px" }}
        >
          <h2
            id="contact-heading"
            className="mb-4 text-3xl font-bold text-text-primary dark:text-text-primary-dark sm:text-4xl"
          >
            Get In Touch
          </h2>
          <p className="mx-auto mb-12 max-w-lg text-lg text-text-secondary dark:text-text-secondary-dark">
            I&apos;m always open to new opportunities and collaborations. Feel
            free to reach out through any of the platforms below.
          </p>
        </motion.div>

        <motion.div
          className="flex flex-wrap items-center justify-center gap-4"
          variants={staggerContainer}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, margin: "-50px" }}
        >
          {profile.socialLinks.map((link) => {
            const Icon = link.icon;
            return (
              <motion.div key={link.name} variants={staggerChild}>
                <motion.div {...hoverScale}>
                  <Button
                    variant="outline"
                    size="lg"
                    asChild
                    className="gap-3 px-6"
                  >
                    <a
                      href={link.url}
                      target={
                        link.url.startsWith("mailto:") ? undefined : "_blank"
                      }
                      rel={
                        link.url.startsWith("mailto:")
                          ? undefined
                          : "noopener noreferrer"
                      }
                      aria-label={link.ariaLabel}
                    >
                      <Icon className="h-5 w-5" aria-hidden="true" />
                      <span>{link.name}</span>
                    </a>
                  </Button>
                </motion.div>
              </motion.div>
            );
          })}
        </motion.div>
      </div>
    </section>
  );
};

export default Contact;
